.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0;

  font: inherit;
  font-family: $ff-raleway;

  color: $color-default-white;
  border-radius: size(100px);
  border: none;

  cursor: pointer;
  user-select: none;
  transition: background-color $trans-default, color $trans-default, opacity $trans-default;
  outline: none;

  &--bg-green {
    background: $color-green;
  }

  &:hover {
    opacity: 0.8;
  }
}
