.link {
  display: inline-flex;

  font-size: inherit;
  line-height: inherit;
  color: inherit;

  border: none;
  background-color: $color-transparent;

  cursor: pointer;

  transition: opacity $trans-default;

  &:hover {
    opacity: 0.8;
  }

  &--underline {
    &::before {
      content: "";

      position: absolute;
      bottom: size(6px);
      left: 0;

      width: 100%;
      opacity: 0.6;
      border-style: solid;
      border-width: size(1px);
      border-color: transparent;
      mask: linear-gradient(90deg, rgba(163, 163, 161, 0) 0%, #A3A3A1 48.44%, rgba(163, 163, 161, 0) 100%) padding-box,
          linear-gradient($color-default-white, $color-default-white);
        mask-composite: exclude;
        background: linear-gradient(90deg, rgba(163, 163, 161, 0) 0%, #A3A3A1 48.44%, rgba(163, 163, 161, 0) 100%) border-box;

      transition: border-color $trans-default, opacity $trans-default, background-color $trans-default;
    }
  }

  &--accent {
    &::before {
      content: "";

      position: absolute;
      bottom: size(6px);
      left: 0;

      width: 100%;
      opacity: 0.6;
      border-style: solid;
      border-width: size(1px);

      background-color: #5DB469;
      opacity: 0.6;
      border-color: #5DB469;
      transition: border-color $trans-default, opacity $trans-default, background-color $trans-default;
    }
  }

}
