.drop-menu {
  display: flex;
  position: fixed;
  z-index: 10;

  width: size(1379px);
  padding: size(41px) size(50px) size(49px) 0;

  background-color: $color-default-white;
  box-shadow: 0 size(10px) size(40px) rgba(15, 68, 22, 0.02);
  border-radius: 0 0 size(15px) size(15px);

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity $trans-default, visibility $trans-default;

  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events:auto;

    @include vp-767 {
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }
  }

  &__list {
    margin: size(16px) size(55px);
    padding: 0;

    list-style: none;
  }

  &__item {
    margin: size(13px) 0;
    color: $color-green2;

    font-size: size(16px);
    line-height: size(22px);

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    padding: size(10px) 0;

    font-weight: 600;

  }

  &__btn {
    max-width: size(210px);
    width: 100%;
    padding: size(20px) size(20px) size(20px) size(10px);

    background: #434341;
    box-shadow: 0 size(10px) size(30px) rgba(15, 68, 22, 0.04), 0 0 size(20px) size(-6px) rgba(24, 85, 34, 0.04);
    border-radius: 0 size(100px) size(100px) 0;

    font-weight: 600;
    font-size: size(16px);
    line-height: size(22px);
  }

  &__right-inner {
    display: none;

    grid-template-columns: repeat(3, max-content);
    grid-column-gap: 50px;

    padding: size(15px) size(40px) size(30px);

    background: $color-default-white;
    box-shadow: inset 0px 6px 15px rgba(14, 69, 21, 0.05);
    border-radius: size(12px);

    &.active {
      display: grid;
    }
  }

  &__sub {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__sub-list {
    margin: 0 0 size(8px);
    padding: 0;

    list-style: none;
  }

  &__sub-link {
    position: relative;

    margin: 0 0 size(10px);
    padding: size(15px) 0 size(8px);

    color: $color-green2;

    font-weight: 600;
    font-size: size(16px);
    line-height: size(22px);

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &.link--accent {
      color: #5DB469;
    }
  }


  &__sub-item {
    margin: size(10px) 0;

    color: #80807C;

    line-height: size(20px);
    font-weight: 400;


    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
