@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: $vp-1920 - 1) {
    @content;
  }
}

@mixin vp-1639 {
  @media (max-width: ($vp-1640 - 1)) {
    @content;
  }
}

@mixin vp-1170 {
  @media (max-width: $vp-1171 - 1) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: ($vp-1024 - 1)) {
    @content;
  }
}

@mixin vp-910 {
  @media (max-width: ($vp-911 - 1)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1)) {
    @content;
  }
}

@mixin vp-480 {
  @media (max-width: ($vp-481 - 1)) {
    @content;
  }
}

