.mob-menu {
  display: none;

  @include vp-767 {
    display: flex;
    align-items: center;
    margin-top: size(7px);
  }

  svg {
    width: size(36px);
    height: size(36px);
  }

  &__toggle {
    position: relative;
    margin-right: size(8px);

    width: size(23px);
    height: size(17px);

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;

      width: 100%;
      height: size(3px);

      background-color: #434341;
      border-radius: size(50px);

      transition: transform $trans-default;
    }

    &::before {
        top: size(-1px);
    }

    &::after {
      bottom: size(1px);
    }

    &.is-active {
      &::before,
      &::after {
          background-color: #434341;

          transition: transform $trans-default;
        }

        &::before {
          top: 50%;

          transform: rotate(-45deg) translateY(-50%);

          @include vp-767 {
            transform: rotate(-45deg) translateY(-50%) scale(0.9);
          }
        }

        &::after {
          bottom: 47%;

          transform: rotate(45deg) translateY(50%);

          @include vp-767 {
            transform: rotate(45deg) translateY(50%) scale(0.9);
          }
        }

      .mob-menu__toggle-line {
        color: $color-default-black;

        transform: scale(0) translateY(-50%);

        transition: transform $trans-default;
      }
    }
  }

  &__toggle-line {
    position: absolute;
    top: size(6.05px);
    left: 0;

    width: 100%;
    height: size(3px);
    border-radius: size(50px);

    background-color: #434341;
    transform: scale(1);
  }

  &__phone {
    margin-right: size(5px);
  }
}
