.card {
  width: size(690px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: flex-end;

  padding: size(50px) size(69px);

  background: $color-default-white;
  box-shadow: 0 size(10px) size(25px) rgba(28, 106, 39, 0.03);
  border-radius: size(12px);

  @include vp-767 {
    width: size(280px);
    margin: 0 size(20px);
    padding: size(25px) size(20px);
  }

  &__title {
    margin: 0 0 size(24px);
    font-weight: 300;
    font-size: size(28px);
    line-height: size(34px);

    color: #232321;
    text-transform: uppercase;
    font-family: $ff-jost;

    @include vp-767 {
      margin-bottom: size(21px);

      font-size: size(16px);
      line-height: size(22px);
    }
  }

  &__info-title {
    margin: 0 0 size(5px);

    font-size: size(16px);
    line-height: size(22px);

    color: #80807C;

    @include vp-767 {
      margin-bottom: 0;

      font-size: size(14px);
      line-height: size(20px);
    }
  }

  &__price-block {
    margin: 0;
  }

  &__price {
    color: $color-green;

    text-transform: uppercase;

    font-size: size(35px);
    line-height: size(42px);
    font-family: $ff-jost;

    @include vp-767 {
      font-size: size(25px);
      line-height: size(30px);
    }
  }

  &__btn {
    width: size(219px);
    align-self: flex-end;
    margin-left: auto;
    background-color: $color-green;
    padding: size(14px);

    font-weight: 600;
    font-size: size(16px);
    line-height: size(22px);

    @include vp-767 {
      width: size(240px);
    }
  }

  &__inner {
    width: size(537px);
    display: grid;
    grid-column-gap: size(30px);
    grid-template-columns: repeat(2, auto);

    @include vp-767 {
      width: size(240px);
      grid-template-columns: 1fr;
      grid-column-gap: 0;
      grid-row-gap: size(10px);
    }
  }

  &__info {
    @include vp-767 {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }

  .rouble {
    margin-left: size(5px);
    color: $color-green;
    font-family: $ff-rouble;

    font-size: size(35px);
    line-height: size(42px);
    text-shadow: 0 0 size(1px) $color-green;

    @include vp-767 {
      font-size: size(25px);
      line-height: size(30px);
    }
  }
}
