.header {
  width: 100%;
  margin: 0 auto;
  padding: size(13px) 0 0;

  background-color: $color-default-white;

  @include vp-767 {
    padding-top: size(10px);

    background-color: #f5f5f5;
  }

  .container {
    max-width: size(1380px);

    @include vp-767 {
      padding: 0 size(20px);
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    padding-bottom: size(15px);

    border-bottom: size(1px) solid #F5F5F5;
  }

  &__logo {
    width: size(167px);
    height: size(35px);
    margin-top: size(8px);

    svg {
      width: size(167px);
      height: size(35px);
    }
  }

  &__info {
    display: flex;
    margin-left: auto;

    @include vp-767 {
      display: none;
    }
  }

  &__address {
    display: grid;

    grid-row-gap: size(5px);

    justify-items: flex-end;
    margin-left: size(20px);

    font-size: size(12px);
    line-height: size(14px);

    &:first-child {
      margin-left: 0;
    }
  }

  &__address-link {
    color: #80807C;
  }

  &__address-text {
    display: flex;
    margin: 0;

    color: #80807C;

    svg {
      flex-shrink: 0;

      width: size(12px);
      height: size(12px);
      margin-right: size(12px);
    }
  }

  &__time {
    margin: 0;
  }

  &__btn {
    align-self: center;
    padding: size(10px) size(25px);

    font-weight: 400;
    line-height: size(20px);
  }

  &__contacts {
    display: grid;
    grid-template-columns:  repeat(2, auto);
    grid-column-gap: size(26px);
    margin-top: size(8px);
    margin-left: size(35px);
  }

  &__phone {
    align-self: center;

    font-weight: 600;
    font-size: size(16px);
    line-height: size(22px);
  }

  &__bottom {
    display: flex;
    justify-content: space-between;

    @include vp-767 {
      &.overlay {
        position: fixed;
        top: size(70px);
        right: 0;
        left: 0;
        z-index: 3;

        width: 100%;
        height: 100%;

        background-color: rgba($color-default-black, 0.5);

        transition: 0.8s;
      }
    }
  }

  &__bottom-list {
    display: flex;
    align-items: center;
    margin: 0 0 0 auto;
    padding: 0;

    list-style: none;

    @include vp-767 {
      display: none;
    }
  }

  &__bottom-item {
    margin-left: size(20px);

    &:first-child {
      margin-left: 0;
    }
  }
}
