
.main-nav {
  display: flex;

  @include vp-767 {
    position: fixed;
    top: size(70px);
    right: size(-220px);
    bottom: 0;
    z-index: 3;

    width: size(220px);
    height: 100%;
    padding-right: size(20px);
    margin-left: auto;

    background-color: #f5f5f5;

    transition: 0.8s;

    &.is-active {
      height: calc(100vh - #{size(69px)});
      top: size(69px);
      right: 0;
      z-index: 3;

      overflow-y: scroll;

      transition: 0.8s;
    }
  }

  &__list {
    display: flex;
    margin: 0 0 0 size(-7px);
    padding: 0;

    list-style: none;

    @include vp-767 {
      width: size(220px);
      margin-left: 0;

      flex-direction: column;
      align-items: flex-end;
    }
  }

  &__item {
    margin-left: size(7px);

    @include vp-767 {
      margin-left: 0;
    }
  }

  &__link {
    align-items: center;
    padding: size(12px) size(7px);

    &--no-pl {
      padding-left: 0;
    }

    svg {
      width: size(7px);
      height: size(5px);
      margin-left: size(2px);

      @include vp-767 {
        display: none;
      }
    }
  }

  &__item--color-green {
    color: $color-green;

    svg {
      width: size(19px);
      height: size(19px);
      margin-right: size(8px);
    }
  }
}
