.page-intro {

  color: #232321;
  background: #F5F5F5;

  .container {
    position: relative;
    overflow: hidden;
  }

  &__title-container {
    position: relative;
    max-width: size(1380px);
    margin: 0 auto;

    @include vp-767 {
      padding-top: size(78px);
    }
  }

  .title {
    position: absolute;
    z-index: 2;
    max-width: size(709px);
    width: 100%;
    top: size(60px);

    @include vp-767 {
      position: relative;
      top: 0;
      padding: 0 size(18px);
    }
  }

  &__decor {
    position: absolute;
    left: size(-147px);
    top: size(-232px);

    width: size(870px);
    height: size(901px);

    @include vp-767 {
      left: size(-763px);
      top: size(-189px);
    }

    img {
      width: size(870px);
      height: size(901px);

      object-fit: contain;
    }
  }
}
