.title {
  margin: 0;

  font-weight: 500;
  font-family: $ff-jost;

  color: inherit;

  &--h1 {
    font-size: size(50px);
    line-height: size(60px);

    @include vp-767 {
      font-size: size(25px);
      line-height: size(30px);
    }
  }

  &__accent {
    color: #6FC67B;
  }

  &--uppercase {
    text-transform: uppercase;
  }
}
