.link-icon {
  display: inline-flex;
  align-items: center;
  padding: 0;

  color: inherit;

  border: none;
  background-color: $color-transparent;

  font: inherit;
  cursor: pointer;
  transition: opacity $trans-default;

  &:hover {
    opacity: 0.8;
  }


  svg {
    width: size(18px);
    height: size(18px);
  }
}
