.slider {

  &__container {
    position: relative;

    @include vp-767 {
      padding-top: size(115px);
    }
  }

  &__slide {
    width: size(1920px);

    @include vp-767 {
      width: size(320px);
    }
  }

  &__inner {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    width: size(1380px);
    margin: 0 auto;

    @include vp-767 {
      flex-direction: column;
      width: size(320px);
    }
  }

  .title {
    margin-bottom: size(41px);
  }

  &__inner-info {
    display: flex;
    margin-bottom: size(58px);

    @include vp-767 {
      margin-bottom: size(23px);
    }
  }

  &__inner-img {
    position: relative;
    z-index: 2;
    width: size(453px);
    height: size(510px);
    margin-right: size(73px);

    @include vp-767 {
      width: size(291px);
      height: size(346px);
      margin-right: 0;
      margin-left: auto;
    }


    img {
      position: absolute;
      width: 100%;
      bottom: 0;

      object-fit: contain;

      @include vp-767 {
        position: relative;
        height: size(373px);
      }
    }
  }

  &__inner-bg {
    position: absolute;

    width: size(798px);
    height: size(511px);
    top: 0;
    right: 0;

    @include vp-767 {
      width: size(160px);
      height: size(426px);
      right: 0;
      top: size(176px);
    }


    img {
      position: absolute;
      width: size(798px);
      height: size(511px);

      @include vp-767 {
        width: size(160px);
        height: size(426px);

        object-fit: contain;
      }
    }
  }

  &__controls {
    position: absolute;
    top: 50%;
    left: size(160px);
    right: size(160px);
    transform: translateY(-50%);
    z-index: 4;

    width: calc(100% - #{size(320px)});
    display: flex;
    justify-content: space-between;


    @include vp-767 {
      width: size(104px);
      top: size(81px);
      left: auto;
      right: size(20px);
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color: $color-transparent;
    padding: 0;
    cursor: pointer;

    width: size(57px);
    height: size(56px);



    &:hover {
      opacity: 0.8;
    }

    @include vp-767 {
      width: size(47px);
      height: size(46px);
    }

    svg {
      width: size(15px);
      height: size(27px);
      fill: none;
      flex-shrink: 0;

      @include vp-767 {
        width: size(10px);
        height: size(22px);
      }
    }

    &--next {
      @include vp-767 {
        background: rgba(255, 255, 255, 0.7);
        border-radius: size(100px);
        transition: opacity $trans-default;
      }

      svg {
        transform: rotate(-180deg);
      }
    }

    &--prev {
      background: rgba(255, 255, 255, 0.7);
      border-radius: size(100px);
      transition: opacity $trans-default;
    }
  }
}
